}

.selectedOption:hover {
  background-color: #2ee59d;
  text-align: left;
}
.flag {
  border-color: black;
}


* {
  box-sizing: border-box;
}
body {
  background-image: linear-gradient(#d4f7e9, white, #d4f7e9);
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: "Fira Sans", sans-serif;
}

body,
html,
.App,
#root {
  width: 100%;
  height: 100%;
}

